// webp
function testWebP(callback) {
   var webP = new Image();
   webP.onload = webP.onerror = function () {
      callback(webP.height == 2);
   };
   webP.src = 'data:image/webp;base64,UklGRjoAAABXRUJQVlA4IC4AAACyAgCdASoCAAIALmk0mk0iIiIiIgBoSygABc6WWgAA/veff/0PP8bA//LwYAAA';
}
testWebP(function (support) {
   if (support == true) {
      document.querySelector('body').classList.add('webp');
   } else {
      document.querySelector('body').classList.add('no-webp');
   }
});
// burger
function burgerMenu() {
   const burger = document.querySelector('.burger')
   const menu = document.querySelector('.nav')
   burger.addEventListener('click', () => {
      if (!menu.classList.contains('active')) {
         menu.classList.add('active')
         burger.classList.add('active-burger')
      } else {
         menu.classList.remove('active')
         burger.classList.remove('active-burger')
      }
   })
   window.addEventListener('resize', () => {
      if (window.innerWidth > 767.98) {
         menu.classList.remove('active')
         burger.classList.remove('active-burger')
      }
   })
}
burgerMenu();
//form
let formBth = document.querySelector('.nav__form-btn')
let formInput = document.querySelector('.nav__form-input')
formBth.addEventListener("click", () => {
   if (formInput.classList.contains('active')) {
      formInput.classList.remove('active')
   } else {
      formInput.classList.add('active')
   }
})
// tabs
function openTab(evt, tabName, path) {
   let i, tabcontent, tablinks;
   let paths = document.getElementsByClassName('hero__place')
   tabcontent = document.getElementsByClassName('hero__content');
   for (i = 0; i < tabcontent.length; i++) {
      tabcontent[i].className = tabcontent[i].className.replace(' active', '');
   }
   tablinks = document.getElementsByClassName('hero__header-item');
   for (i = 0; i < tablinks.length; i++) {
      tablinks[i].className = tablinks[i].className.replace(' active', '');
   }
   for (i = 0; i < paths.length; i++) {
      paths[i].className = paths[i].className.replace(' active', '');
   }
   document.getElementById(tabName).className += ' active';
   document.getElementById(path).className += ' active';
   evt.currentTarget.className += ' active';
}
//scroll
const links = document.querySelectorAll('.scroll');
for (const link of links) {
   link.addEventListener('click', clickHandler);
}
function clickHandler(e) {
   e.preventDefault();
   const theLink = this.getAttribute('href');
   document.querySelector(theLink).scrollIntoView({
      behavior: 'smooth'
   });
}
