// input count
let up = document.querySelectorAll('#plus')
let down = document.querySelectorAll('#minus')
let input = document.querySelectorAll('.slide__info-input')
let sum = document.querySelector('#sum')
for (let i = 0; i < input.length; i++) {
   down[i].addEventListener('click', () => {
      input[i].value++
      let v = parseInt(input[i].value);
      if (v < 1) input[i].value = 1;
      if (v > 9) input[i].value = 9;
   })
   up[i].addEventListener('click', () => {
      input[i].value--
      let v = parseInt(input[i].value);
      if (v < 1) input[i].value = 1;
      if (v > 9) input[i].value = 9;
   })
}
